export default {
	components: {
	},
	data() {
		return {
			userInfo: {},	// 用户信息
		}
	},
	// 监听页面卸载， 监听页面的卸载， 当前处于A页面，点击返回按钮时，则将是A页面卸载、
	onUnload() {
	},
	onReady(){
	},
	onShow(){
		let that = this;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				that.userInfo = res.data;
			}
		});
	},
	onLoad(){
	},
	// 监听页面的隐藏,当从当前A页跳转到其他页面，那么A页面处于隐藏状态。
	onHide(){
	},
	methods: {
		// 用户发帖--内容和图片, 将该帖子关联该用户
		publish(){
			let that = this;
			if(that.userInfo.profile.length > 150){
				uni.showToast({
				    title: '长度不超过150个字符',
				    icon: 'none'
				});
				return 
			}
			that.Bmob.checkMsg(that.userInfo.profile).then(res => {
				uni.showLoading({
					title: '加载中'
				});
				const query = that.Bmob.Query('_User');
				query.set('id', that.userInfo.objectId) //需要修改的objectId
				query.set('profile', that.userInfo.profile)
				query.save().then(res => {
					query.get(that.userInfo.objectId).then(res1 => {
						//更新登陆状态
						uni.setStorage({
						  key: 'userInfo',
						  data: res1,
						  success: function () {
							console.log('保存成功')
							uni.navigateBack()
						  }
						});
					})
				}).catch(err => {
					console.log(err)
				})
			}).catch(err => {
				let tips = '错误类型：' + err.code + '，' + err.error;
				if(err.code === 10007){
					tips = '输入内容含有敏感词，请文明用语'
				}
				uni.showToast({
					title: tips,
					icon: 'none'
				})
			})
			
		},
	},
}
